import React from 'react';
import Section from "../../Atomics/Section";
import {useUserInfo} from "../../Providers/FireBaseProvider";

const Profile = () => {
    const userInfo = useUserInfo();

    return (
        <Section title={'Profil'}>
            Name: {userInfo.surname} {userInfo.lastname} <br />
            Email Adresse: {userInfo.email} <br />
        </Section>
    );
};

export default Profile;